import React from 'react'
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Container from '@material-ui/core/Container';
const Metrics1 = ({setBaseScore, baseScore, AttackVector, setAttackVector, InflC, InflI, InflA, setInflC,
  setInflI, setInflA, hard, setHard, interact, setInterac, scope, setScope, privelege, setPrivelege
}) => {
    let av = [0.85, 0.62, 0.55, 0.2][AttackVector]
    let ac = [0.77, 0.44][hard]
    let t_pr = [0.85, 0.62, 0.27][privelege]
    let pr = 0
    if( t_pr === 0.62){
      if(scope === 1){pr = 0.68
      }else{pr = 0.62}
    }else if(t_pr === 0.27){
      if(scope === 1){pr = 0.50
      }else{pr = 0.27}
    }else{pr = 0.85}
    let ui = [0.85, 0.62][interact]
    let s = [0, 1][scope]
    let c = [0, 0.22, 0.56][InflC]
    let i = [0, 0.22, 0.56][InflI]
    let a = [0, 0.22, 0.56][InflA]

    let ImpactBase = 1-((1-c)*(1-i)*(1-a))
    let Exploitability = 8.22 *av*ac*pr*ui
    let Impact = 0
    if(s === 0){
      Impact = 6.42*ImpactBase
    }else{
      Impact=7.52*(ImpactBase-0.029)-3.25*(ImpactBase - 0.02)**15
    }
    if(Impact <= 0){
      setBaseScore(0)
    }else{
      if(s===0){
        setBaseScore(Number(Math.min((Impact+Exploitability), 10)).toFixed(2))
      }else{
        setBaseScore(Number(Math.min(1.08*(Impact+Exploitability), 10)).toFixed(1))
      }
    }
    console.log(av)
    console.log(ac)
    console.log(pr)
    console.log(ui)



    return(
        <div>
        <Container>
        Базовые метрики: {Number(baseScore).toFixed(1)}
        </Container>
        <FormControl>
        <FormLabel component="legend">Вектор Атаки (AV):</FormLabel>
        <RadioGroup value={AttackVector} onChange={(event) => setAttackVector(parseInt(event.target.value))}>
          <FormControlLabel value={2} control={<Radio />} label="Физический" />
          <FormControlLabel value={0} control={<Radio />} label="Локальный" />
          <FormControlLabel value={1} control={<Radio />} label="Смежная сеть" />
          <FormControlLabel value={2} control={<Radio />} label="Сетевой" />
        </RadioGroup>
        <FormLabel component="legend">Сложность атаки (AC):</FormLabel>
      <RadioGroup value={hard} onChange={(event) => setHard(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Высокая" />
        <FormControlLabel value={1} control={<Radio />} label="Низкая" />
      </RadioGroup>
      <FormLabel component="legend">Уровень привилегий (PR):</FormLabel>
      <RadioGroup value={privelege} onChange={(event) => setPrivelege(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не требуется" />
        <FormControlLabel value={1} control={<Radio />} label="Низкий" />
        <FormControlLabel value={2} control={<Radio />} label="Высокий" />
      </RadioGroup>
      <FormLabel component="legend">Взаимодействие с пользователем (UI):</FormLabel>
      <RadioGroup value={interact} onChange={(event) => setInterac(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не требуется" />
        <FormControlLabel value={1} control={<Radio />} label="Требуется" />
      </RadioGroup>
      <FormLabel component="legend">Влияние на другие компоненты системы (С):</FormLabel>
      <RadioGroup value={scope} onChange={(event) => setScope(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не оказывается" />
        <FormControlLabel value={1} control={<Radio />} label="Оказывается" />
      </RadioGroup>
      <FormLabel component="legend">Влияние на конфиденциальность (С):</FormLabel>
      <RadioGroup value={InflC} onChange={(event) => setInflC(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не оказывается" />
        <FormControlLabel value={1} control={<Radio />} label="Частичная" />
        <FormControlLabel value={2} control={<Radio />} label="Полное" />
      </RadioGroup>
      <FormLabel component="legend">Влияние на целостность (I):</FormLabel>
      <RadioGroup value={InflI} onChange={(event) => setInflI(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не оказывается" />
        <FormControlLabel value={1} control={<Radio />} label="Частичная" />
        <FormControlLabel value={2} control={<Radio />} label="Полное" />
      </RadioGroup>
      <FormLabel component="legend">Влияние на доступность (A):</FormLabel>
      <RadioGroup value={InflA} onChange={(event) => setInflA(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не оказывается" />
        <FormControlLabel value={1} control={<Radio />} label="Частичная" />
        <FormControlLabel value={2} control={<Radio />} label="Полное" />
      </RadioGroup>
      </FormControl>
      </div>
    )
}
export default Metrics1