import React from 'react'
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import FormLabel from '@material-ui/core/FormLabel';
import Container from '@material-ui/core/Container';
const Metrics3 = ({baseScore, MAttackVector, setMAttackVector, mhard, setMHard, mPrivelege, setMPrivelege,
  mInterac, setMInterac, setRecAva, recAva, setRecI, recI,setRecConf, recConf, setCInflA, CInflA, setCInflI,
  CScope, setCScope, CInflC, setCInflC, CInflI, interact, scope, InflC, InflI, InflA, hard, AttackVector,
  use, fix, proof, privelege 
}) => {
    let ms = [[0, 1][scope], 0, 1]
    let t_pr = [0.85, 0.62, 0.27][privelege]
    let pr = 0
    if( t_pr === 0.62){
      if(scope === 1){pr = 0.68
      }else{pr = 0.62}
    }else if(t_pr === 0.27){
      if(scope === 1){pr = 0.50
      }else{pr = 0.27}
    }else{pr = 0.85}
    let t_mpr = [pr, 0.85, 0.62, 0.27]
    let mpr = 0
    if(t_mpr===0.62){
      if(ms === 1){
        mpr = 0.68
      }else{
        mpr = 0.62
      }
    }else if(t_mpr === 0.27){
      if(ms === 1){
        mpr = 0.50
      }else{
        mpr = 0.27
      }
    }
    let mc = [[0, 0.22, 0.56][InflC], 0, 0.22, 0.56][CInflC]
    let mi = [[0, 0.22, 0.56][InflI], 0, 0.22, 0.56][CInflI]
    let ma = [[0, 0.22, 0.56][InflA], 0, 0.22, 0.56][CInflA]
    let mui = [[0.85, 0.62][interact], 0.85, 0.62][mInterac]
    let mav = [[0.85, 0.62, 0.55, 0.2][AttackVector], 0.85, 0.62, 0.55, 0.2][MAttackVector]
    let mac = [[0.77, 0.44][hard], 0.77, 0.44][mhard]
    let cr = [1, 1.5, 1, 0.5][recConf]
    let ir = [1, 1.5, 1, 0.5][recI]
    let ar = [1, 1.5, 1, 0.5][recAva]
    let e = [1, 0.91, 0.94, 0.97, 1][use]
    let rl = [1, 0.95, 0.96, 0.97, 1][fix]
    let rc = [1, 0.92, 0.96, 1][proof]
    let EnvironmentalScore = 0
    let MImpactBase = Math.min((1-(1-mc*cr)*(1-mi*ir)*(1-ma*ar)), 0.915)
    let MImpact = 0
    let MExp = 8.22*mav*mac*mpr*mui
    if(ms === 0){
      MImpact = 6.42*MImpactBase
    }else{
      MImpact = 7.52*(MImpactBase-0.029)-3.25*(MImpactBase-0.02)**15
    }
    if(MImpact <= 0){
      EnvironmentalScore = 0
    }else{
      if(ms === 0){
        EnvironmentalScore = (Math.min(MImpact+MExp, 10).toFixed(1)*e*rl*rc ).toFixed(1)
      }else{
        EnvironmentalScore= (Math.min(1.08*(MImpact+MExp), 10)).toFixed(1)*e*rl*rc
      }
    }
    
    return(
        <div>
        <Container>
        Контекстные метрики: {Number(EnvironmentalScore.toFixed(1))}
        </Container>
        <FormControl>
        <FormLabel component="legend">Скорректированный вектор атаки (MAV):</FormLabel>
        <RadioGroup value={MAttackVector} onChange={(event) => setMAttackVector(parseInt(event.target.value))}>
          <FormControlLabel value={0} control={<Radio />} label="Не определено" />
          <FormControlLabel value={1} control={<Radio />} label="Низкий" />
          <FormControlLabel value={2} control={<Radio />} label="Высокий" />
        </RadioGroup>
        <FormLabel component="legend">Скорректированная сложность атаки (MAC):</FormLabel>
      <RadioGroup value={mhard} onChange={(event) => setMHard(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не требуется" />
        <FormControlLabel value={1} control={<Radio />} label="Низкий" />
        <FormControlLabel value={2} control={<Radio />} label="Высокий" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированный уровень привилегий (MPR):</FormLabel>
      <RadioGroup value={mPrivelege} onChange={(event) => setMPrivelege(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Нет" />
        <FormControlLabel value={2} control={<Radio />} label="Низкий" />
        <FormControlLabel value={3} control={<Radio />} label="Высокий" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированное взаимодействие с пользователем (MUI):</FormLabel>
      <RadioGroup value={mInterac} onChange={(event) => setMInterac(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Не требуется" />
        <FormControlLabel value={2} control={<Radio />} label="Требуется" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированное влияние на другие компоненты системы (MS):</FormLabel>
      <RadioGroup value={CScope} onChange={(event) => setCScope(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Не оказывается" />
        <FormControlLabel value={2} control={<Radio />} label="Оказывается" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированное влияние на конфиденциальность (MС):</FormLabel>
      <RadioGroup value={CInflC} onChange={(event) => setCInflC(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированное влияние на целостность (MI):</FormLabel>
      <RadioGroup value={CInflI} onChange={(event) => setCInflI(parseInt(event.target.value))}>
      <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      <FormLabel component="legend">Скорректированное влияние на доступность (MA):</FormLabel>
      <RadioGroup value={CInflA} onChange={(event) => setCInflA(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      <FormLabel component="legend">Требование к конфиденциальности (CR):</FormLabel>
      <RadioGroup value={recConf} onChange={(event) => setRecConf(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      <FormLabel component="legend">Требование к целостности (IR):</FormLabel>
      <RadioGroup value={recI} onChange={(event) => setRecI(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      <FormLabel component="legend">Требование к доступности (AR):</FormLabel>
      <RadioGroup value={recAva} onChange={(event) => setRecAva(parseInt(event.target.value))}>
        <FormControlLabel value={0} control={<Radio />} label="Не определено" />
        <FormControlLabel value={1} control={<Radio />} label="Низкое" />
        <FormControlLabel value={2} control={<Radio />} label="Среднее" />
        <FormControlLabel value={3} control={<Radio />} label="Высокое" />
      </RadioGroup>
      </FormControl>
      </div>
    )
}
export default Metrics3