import React, {useState} from 'react';
import Metrics1 from "./Components/Metrics1"
import Metrics2 from "./Components/Metrics2"
import Metrics3 from "./Components/Metrics3"
import './App.css';
import Grid from '@material-ui/core/Grid';

function App() {
  const [hard, setHard] = useState(0)
  const [interact, setInterac] = useState(0)
  const [InflC, setInflC] = useState(0)
  const [InflI, setInflI] = useState(0)
  const [InflA, setInflA] = useState(0)
  const [baseScore, setBaseScore] = useState(0)
  const [use, setUse] = useState(0)
  const [fix, setFix] = useState(0)
  const [proof, setProof] = useState(0)
  const[scope, setScope] = useState(0)
  const[privelege, setPrivelege] = useState(0)
  const[mhard, setMHard] = useState(0)
  const[AttackVector, setAttackVector] = useState(0)
  const[CInflI, setCInflI] = useState(0)
  const[MAttackVector, setMAttackVector] = useState(0)
  const[mPrivelege, setMPrivelege] = useState(0)
  const[mInterac, setMInterac] = useState(0)
  const[recAva, setRecAva] = useState(0)
  const[recI, setRecI] = useState(0)
  const[recConf, setRecConf] = useState(0)
  const[CInflA, setCInflA] = useState(0)
  const[CScope, setCScope] = useState(0)
  const[CInflC, setCInflC] = useState(0)





  return (
    <div className="App">
      <Grid container spacing={2} 
      direction="row"
      justify="space-around"
      alignItems="flex-start">
        <Grid item className="item">
          <Metrics1 setBaseScore={setBaseScore} baseScore={baseScore} AttackVector={AttackVector} setAttackVector={setAttackVector}
           InflC={InflC} InflI={InflI} InflA={InflA} setInflC={setInflC} setInflI={setInflI} setInflA={setInflA}
           hard={hard} setHard={setHard} interact={interact} setInterac={setInterac} scope={scope} setScope={setScope}
          privelege = {privelege} setPrivelege={setPrivelege}
            />
        </Grid>
        <Grid item className="item">
          <Metrics2 baseScore={baseScore} use={use} setUse={setUse} fix={fix} setFix={setFix} proof={proof} setProof={setProof}/>
        </Grid>
        <Grid item className="item">
          <Metrics3 baseScore={baseScore} MAttackVector={MAttackVector} setMAttackVector={setMAttackVector}
           mhard={mhard} setMHard={setMHard} mPrivelege={mPrivelege} setMPrivelege={setMPrivelege} mInterac={mInterac}
           setMInterac = {setMInterac} setRecAva={setRecAva} recAva={recAva} setRecI={setRecI} recI={recI}
           setRecConf={setRecConf} recConf={recConf} setCInflA={setCInflA} CInflA={CInflA} setCInflI={setCInflI}
           CInflI={CInflI} interact={interact} scope={scope} InflC={InflC} InflI={InflI} InflA={InflA}
           hard={hard} AttackVector={AttackVector} use={use} fix={fix} proof={proof} privelege={privelege} 
           CScope={CScope} setCScope={setCScope} CInflC={CInflC} setCInflC={setCInflC}
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
